.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  font-family: Arial, Helvetica, sans-serif;
}

body {
  margin: 0%;
  padding: 0%;
  background-color: #171717;
}

.header {
  flex: 30%;
  background-color: #444444;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .inputContainer {
  display: flex;
  flex-direction: column;
}

.header input {
  width: 300px;
  height: 40px;
  border: none;
  border-bottom-left-radius: 16px;
  border-top-left-radius: 16px;
  padding-left: 10px;
  font-size: 17px;
  border: 1px solid #40394a;
}

.header button {
  width: 70px;
  height: 87px;
  border: none;
  border-bottom-right-radius: 16px;
  border-top-right-radius: 16px;
  padding-left: 10px;
  cursor: pointer;
}

.todoList {
  flex: 70%;
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
}

.task {
  width: 500px;
  height: 50px;
  display: flex;
  color: white;
  margin: 15px;
}

.task .content {
  flex: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.task .content span {
  display: grid;
  place-items: center;
  border: 1px solid #171717;
  border-radius: 10px;
  width: 100%;
  height: 100%;
  font-size: 18px;
  border-right: none;
  background-color: #da0037;
}

.task button {
  flex: 20%;
  height: 100%;
  border: none;
  background-color: #1b1b2f;
  border-radius: 16px;
  color: white;
  cursor: pointer;
}
